.footer {
    font-size: 12px;
    line-height: 20px;
    text-align: right;
    border-top: 1px solid #c0c2c3;
    position: absolute;
    width: 100%;
    bottom: -50px;
}

.footer-link {
    cursor:pointer;
    text-align: right;
    margin-right: 5px;
}
.center {
    position: absolute;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
    width: 50%;
    text-align: center;
    transform: translate(-50%, 0);
}

.pad {
    padding-bottom: 20px;
    padding-top: 50px;
  }
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  background-color: #eaeeee !important;
  color: black !important;
  font-family: 'Roboto' !important;
  font-weight: 300 !important;
  font-style: normal;
  overflow-x: hidden;
  font-size: 0.8vw !important;
  height: 100%;
  margin: 0;
  padding: 0;
}

html {
  margin: 0;
  padding: 0;
  height: 100%;
}

#root {
  min-height: 100%;
  position: relative;
}

.code-area {
  min-height: 300px;
  min-width: 300px;
  margin-top: 50px;
  border: 1px solid rgb(190, 190, 190);
  padding: 10px;
  border-radius: 4px;
  resize: none;
  overflow: auto;
}

.code-area::-moz-selection {
  color: black;
  background: white;
}

.code-area::selection {
  color: black;
  background: white;
}

input {
  border: 1px solid rgb(190, 190, 190);
  padding-left: 5px;
  border-radius: 4px;
}

.input-container {
  text-align: right;
}

.margin-10 {
  margin-bottom: 10px;
}

.margin-20 {
  margin-bottom: 20px;
}

.rc-slider-rail {
  background: #b0b0b0;
}

.nav-link {
  cursor: pointer;
}

.align-center {
  text-align: center;
}

.align-left {
  text-align: left;
}

.full-width {
  width: 100%;
}

.vertical-center {
  -webkit-align-self: center;
          align-self: center;
}

.vertical-center-items {
  -webkit-align-items: center;
          align-items: center;
}

.align {
  display: inline-block;
}

.right-5 {
  margin-right: 5px;
}

.right-25 {
  margin-right: 2.5%;
}

.left-10 {
  margin-left: 10px;
}

.number-input {
  width: 100%;
}

.top-50 {
  margin-top: 50px;
}

.top-20 {
  margin-top: 20px;
}

.top-10 {
  margin-top: 10px;
}

.chrome-picker {
  display: inline-block;
  margin-top: 20px;
  margin-bottom: 20px;
}

.title {
  font-size: 32px;
  display: inline-block;
  vertical-align: middle;
}

.tooltip-select {
  cursor: pointer;
  color: #5a6268;
  display: inline-block;
  vertical-align: middle;
  margin-left: 10px;
  font-size: 16px;
}

.hide-show {
  cursor: pointer;
}

.hidden {
  visibility: hidden;
  opacity: 1.0; 
}

.backdrop {
   border:  1px solid gray;
   padding: 10px;
}

.no-margin {
  margin: 0px;
}
